"use client";

import React from "react";
import { motion } from "framer-motion";
import { Sparkles } from "lucide-react";

type Props = { step: number; total: number };

const ProgressHeader: React.FC<Props> = ({ step, total }) => {
  const progress = Math.round(((step + 1) / total) * 100);
  return (
    <div className="sticky top-0 bg-white/90 backdrop-blur border-b px-4 py-2 z-20">
      <div className="flex justify-between items-center">
        <div className="flex items-center gap-2">
          <Sparkles className="h-5 w-5 text-green-600" />
          <span className="font-serif">Assessment</span>
        </div>
        <div className="text-sm text-gray-600">
          Step {step + 1} of {total}
        </div>
      </div>
      <div className="w-full h-2 bg-gray-200 rounded mt-2 overflow-hidden">
        <motion.div
          className="h-2 bg-gradient-to-r from-green-600 to-lime-500"
          animate={{ width: `${progress}%` }}
          transition={{ type: "tween", duration: 0.3 }}
        />
      </div>
    </div>
  );
};

export default ProgressHeader;
