"use client";

import React, { useEffect, useRef, useState } from "react";
import { Mic, Square, Play, Pause, Trash2 } from "lucide-react";

type Props = { onAttach?: (url: string | null) => void };

const VoiceRecorder: React.FC<Props> = ({ onAttach }) => {
  const mediaRecorderRef = useRef<any>(null);
  const chunksRef = useRef<BlobPart[]>([]);
  const [recording, setRecording] = useState(false);
  const [permissionError, setPermissionError] = useState<string | null>(null);
  const [audioURL, setAudioURL] = useState<string | null>(null);
  const [elapsed, setElapsed] = useState(0);
  const timerRef = useRef<any>(null);
  const audioRef = useRef<HTMLAudioElement | null>(null);
  const [playing, setPlaying] = useState(false);

  useEffect(() => {
    onAttach?.(audioURL || null);
  }, [audioURL]);

  const start = async () => {
    setPermissionError(null);
    setAudioURL(null);
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      const mr = new MediaRecorder(stream);
      chunksRef.current = [];
      mr.ondataavailable = (e) => {
        if (e.data && e.data.size > 0) chunksRef.current.push(e.data);
      };
      mr.onstop = () => {
        const blob = new Blob(chunksRef.current, { type: "audio/webm" });
        setAudioURL(URL.createObjectURL(blob));
        chunksRef.current = [];
        stream.getTracks().forEach((t) => t.stop());
      };
      mediaRecorderRef.current = mr;
      mr.start();
      setRecording(true);
      setElapsed(0);
      timerRef.current = setInterval(() => setElapsed((s) => s + 1), 1000);
    } catch (err: any) {
      setPermissionError(err?.message || "Microphone not available.");
    }
  };

  const stop = () => {
    mediaRecorderRef.current?.stop();
    setRecording(false);
    if (timerRef.current) clearInterval(timerRef.current);
  };

  const reset = () => {
    setAudioURL(null);
    setElapsed(0);
  };

  const mmss = (s: number) =>
    `${String(Math.floor(s / 60)).padStart(2, "0")}:${String(s % 60).padStart(
      2,
      "0"
    )}`;

  return (
    <div className="rounded-2xl border border-slate-300 p-3 bg-slate-50">
      <div className="flex items-center justify-between gap-2">
        <div>
          <p className="text-sm text-slate-700 font-medium">
            Record a quick answer (optional)
          </p>
          {permissionError && (
            <p className="text-xs text-amber-700 mt-1">{permissionError}</p>
          )}
        </div>
        <div>
          {!recording ? (
            <button
              onClick={start}
              className="px-3 py-2 rounded-full bg-lime-600 text-white flex gap-2 items-center"
            >
              <Mic className="h-4 w-4" /> Record
            </button>
          ) : (
            <button
              onClick={stop}
              className="px-3 py-2 rounded-full bg-red-600 text-white flex gap-2 items-center"
            >
              <Square className="h-4 w-4" /> Stop
            </button>
          )}
        </div>
      </div>
      <div className="mt-2 text-xs text-slate-600 flex justify-between">
        <span>
          {recording ? "Recording…" : audioURL ? "Recorded" : "No recording"}
        </span>
        <span className="font-mono">{mmss(elapsed)}</span>
      </div>
      {audioURL && (
        <div className="mt-2 flex gap-2 items-center">
          <audio ref={audioRef} src={audioURL} />
          {!playing ? (
            <button
              onClick={() => {
                setPlaying(true);
                audioRef.current?.play();
              }}
              className="px-2 py-1 border rounded flex items-center gap-2"
            >
              <Play className="h-4 w-4" /> Play
            </button>
          ) : (
            <button
              onClick={() => {
                audioRef.current?.pause();
                setPlaying(false);
              }}
              className="px-2 py-1 border rounded flex items-center gap-2"
            >
              <Pause className="h-4 w-4" /> Pause
            </button>
          )}
          <button
            onClick={() => {
              reset();
              onAttach?.(null);
            }}
            className="px-2 py-1 border rounded flex items-center gap-2"
          >
            <Trash2 className="h-4 w-4" /> Remove
          </button>
        </div>
      )}
    </div>
  );
};

export default VoiceRecorder;
